@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
body {
    font-family: 'Mukta', sans-serif;
    background: #222831;
    color: #1e212d;
}

p a {
    text-decoration: underline;
}

a {
    text-decoration: none;
    color: #cbbcb1;
}
.contact {
    text-align: center;
    margin-top: 200px;
    margin-bottom: 10px;
}

.fa-3x {
    margin: 10px 5px 0 5px;
    color: #eeeeee;
}

.fa-3x:hover {
    color: #00adb5;
}

.copied-alert {
    color: #00adb5;
}

html.sr .load-hidden {
    visibility: hidden;
}

.main-text {
    padding-top: 175px;
    padding-left: 100px;
    height: 100vh;
}

.head-one {
    color: #eeeeee;
}

.head-two, .about-text {
    color: #00adb5;
}

.arrow-down {
    margin-top: 550px;
    margin-left: 45vw;
    opacity: 50%;
}
h5 {
    text-align: center;
}

.card {
    border: 5px solid #393e46;
    width: 18rem;
    height: 350px;
    padding: 5px;
    margin: 25px;
    background: #393e46;
    border-radius: 10px;
}

.card-title {
    color: #eeeeee;
}

.card:hover {
    opacity: 40%;
    color: #00adb5;
}

a:hover {
    text-decoration: none;
}

.card-div {
    margin-top: 50px;
}

.card:hover .card-title {
    color: #00adb5;
}

.card-title {
    font-size: 1.4rem;
}

.portfolio-title {
    color: #00adb5;
    text-align: center;
    padding: 10px 0;
    background-color: white;
    width: 25%;
    height: 50px;
    margin: 0 auto 0 auto;
    background: #393e46;
    border-radius: 25px;
}

.resume-link {
    color: #00adb5;
    text-align: center;
    padding: 10px 0;
    background-color: white;
    width: 25%;
    height: 50px;
    margin: 100px auto 0 auto;
    background: #393e46;
    border-radius: 25px;
}

.resume-link:hover {
    opacity: 40%;
}
html.sr .load-hidden {
    visibility: hidden;
}

.portfolio {
    margin-top: 50vh;
}
